@import './variables/colors';
@import './styles/button.scss';


.App {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding-bottom: 64px;
  overflow: hidden;
  background-color: $color-cloud;

  .container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  }

  section {
    padding-bottom: 30px;

    &.head {
      padding: 0;
    }
  }

  .overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 0;
    background-color: rgba(0, 0, 0, .50);
  }

  .head p,
  h1, h2, h3, h4 {
    color: $color-purple;
  }
  p {
    color: $color-purple_grey;
  }

  ul {
    list-style: none;
  }
  .nav-link {
    position: relative;
    color: $color-purple_grey;
    font-weight: bold;
    list-style: none;
    transition: all 0.3s ease;
    
    &.active, &:hover {
      color: $color-red_deep;
      cursor: pointer;
      transition: all 0.3s ease;
    }
  }
}

@media only screen and (max-width: 767px) {
	.App {
    padding-bottom: 112px;

    section.head,
    #about section.head {

			.head-text {

				.logo {
					width: 100px;
				}

				h1 {
					font-size: 60px !important;
				}
        h2 {
					font-size: 60px !important;
				}
				h4 {
					font-size: 20px !important;
				}
			}
		}

    p {
      font-size: 16px !important;
    }
  }
}